body {
  background-color: #222831;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.interface {
  height: 60vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.formDiv {
  width: 160%;
  height: 70px;
  position: relative;
  top: -40%;
  left: -80px;
}

.formDiv input {
  width: 100%;
  height: 100%;
  color: #30475e;
  padding-top: 30px;
  font-size: 1.5rem;
  border: none;
  outline: none;
}

.formDiv label {
  position: absolute;
  bottom: 0px;
  left: -8px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  /* border-bottom:2px solid #30475e; */
}

.content-comments {
  font-size: 1.5rem;
}

.formDiv label::after {
  content: "";
  position: absolute;
  left: 0px;
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 0px solid #f05454;
  transform: translateY(-100%);
}

input::-webkit-input-placeholder {
  color: red;
}

input:-ms-input-placeholder {
  color: red;
}

input::-ms-input-placeholder {
  color: red;
}

input::placeholder {
  color: red;
}

#input-form {
  border-radius: 20px;
}

.content-comments {
  position: absolute;
  bottom: 22px;
  left: 30%;
  align-items: center;
  transition: all 0.01s ease;
}

.formDiv input:focus + .label-comments .content-comments,
.formDiv input:valid + .label-comments .content-comments {
  transform: translateY(-120%);
  visibility: hidden;
  font-size: 1.5rem;
  color: #222831;
}

.formDiv input:focus + .label-comments::after,
.formDiv input:valid + .label-comments::after {
  /* display:none; */
  transform: translateY(0%);
  content: "Typing ...";
  font-size: 1.5rem;
  /* border-bottom:4px solid #f05454; */
}

h1 {
  color: #e8e8e8;
}

h2 {
  margin-left: 170px;
